import React from 'react'
import "./Card.scss"
import CardHeader from './Header/CardHeader'
import CounterContainer from './CounterContainer/CounterContainer'


const Card = (props) => {
  // Using object destructuring method to make things easier
  const {data} = props
  // Actual card as a container for header data and counter container
  return (
    <div className='card'>
      <div className='card__header'>
        {/* passing down values from the prop object to card's header component */}
        <CardHeader name={data.name} role={data.role}/>
      </div>
      <div className='card__counter'>
        <CounterContainer/>
      </div>
    </div>
  )
}

export default Card